import { Request, Response, NextFunction } from 'express'

// Block requests for things that are not here anyway, bye-bye bots
const blockUnwantedRequests = (req: Request, res: Response, next: NextFunction): void => {
  const unwantedPattern = /\.(php|exe|cmd|ps1|txt|pdf|doc|docx|xls|xlsx|ppt|pptx)$/
  if (unwantedPattern.test(req.url)) {
    res.status(404).send('Not Found')
  }
  else {
    next()
  }
}

export default blockUnwantedRequests
