import buildDebug from 'debug'
import { Request, Response, NextFunction } from 'express'

import { Logger } from '@verdaccio/types'

const debug = buildDebug('verdaccio:plugin:pro:middleware:redirect')

// Redirect NPM-style URL to web UI
const redirectNpmStyleUrl = (logger: Logger) => {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  return (req: Request, res: Response, _next: NextFunction): void => {
    debug('redirect from %o', req.url)
    const redirectTo = '/-/web/detail' + req.url
    logger.info({ redirectTo }, 'Redirecting to @{redirectTo}')
    debug('redirect to %o', redirectTo)
    res.redirect(redirectTo)
  }
}

export default redirectNpmStyleUrl
