import { Request, Response, NextFunction } from 'express'

// Set security headers for all responses
const setSecurityHeaders = (req: Request, res: Response, next: NextFunction): void => {
  if (req.protocol === 'https' || req.get('X-Forwarded-Proto') === 'https') {
    // TODO: increase max-age from 1 hour to 31536000 (1 year)
    res.setHeader('Strict-Transport-Security', 'max-age=86400; includeSubDomains; preload')
  }

  res.setHeader('Content-Security-Policy',
    'default-src \'self\'; '
    + 'script-src \'self\' \'unsafe-inline\' \'unsafe-eval\'; ' // Review unsafe-* directives
    + 'style-src \'self\' \'unsafe-inline\'; '
    + 'img-src \'self\' https:; '
    + 'connect-src \'self\'; '
    + 'font-src \'self\'; '
    + 'object-src \'none\'; '
    + 'base-uri \'self\'; '
    + 'frame-ancestors \'none\';')

  res.setHeader('Permissions-Policy', 'accelerometer=(), camera=(), geolocation=(), gyroscope=(), magnetometer=(), microphone=(), payment=(self), usb=(), fullscreen=(self), vibrate=()')
  res.setHeader('Referrer-Policy', 'strict-origin-when-cross-origin')
  res.setHeader('X-Robots-Tag', 'noindex')
  res.setHeader('X-Powered-By', 'Verdaccio')
  next()
}

export default setSecurityHeaders
